import { boolean, object, string } from "yup";

export const userSchema = object({
  name: string().required(",  "),
  email: string()
    .email(" e-mail")
    .required(",  e-mail"),
  phone: string().required(",   "),
  agreement: boolean()
    .oneOf([true], ",     ")
    .required(
      ",      "
    ),
});
